a {
  text-decoration: none;
  color: #E0ECFF;
}
* {
  box-sizing: border-box;
}
.testnet-practice {
  border-top: 1px solid #203B60;
  font-weight: normal;
}
.testnet-practice .detailed-rules a {
  color: #93A1B8;
  text-decoration: underline;
}
.testnet-practice .practice-info {
  width: 1250px;
  height: 826px;
  border: 1px solid #203B60;
  margin: 0 auto;
  margin-top: 90px;
  background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);
}
.testnet-practice .practice-info .practice-time,
.testnet-practice .practice-info .practice-info-box,
.testnet-practice .practice-info .practice-title {
  width: 1147px;
  margin: 0 auto;
}
.testnet-practice .practice-info .practice-title {
  margin-top: 43px;
  color: #FFFFFF;
  font-size: 32px;
}
.testnet-practice .practice-info .practice-time {
  margin-top: 8px;
  font-size: 14px;
}
.testnet-practice .practice-info .practice-info-box {
  margin-top: 58px;
  display: flex;
  justify-content: space-between;
}
.testnet-practice .practice-info .practice-info-box .practice-info-left {
  width: 536px;
}
.testnet-practice .practice-info .practice-info-box .practice-info-left .total-rewards-pool {
  width: 100%;
  height: 400px;
  background-image: url('./img/reward.png');
  background-size: 100% 100%;
}
.testnet-practice .practice-info .practice-info-box .practice-info-left .total-rewards-pool .total-rewards-box {
  width: 299px;
  height: 188px;
  padding: 64px 0px 0px 47px;
}
.testnet-practice .practice-info .practice-info-box .practice-info-left .total-rewards-pool .total-rewards-box .deri-logo {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: #5577FD;
  display: flex;
  justify-content: center;
  align-items: center;
}
.testnet-practice .practice-info .practice-info-box .practice-info-left .total-rewards-pool .total-rewards-box .total-rewards-num {
  margin-top: 36px;
  font-weight: 600;
}
.testnet-practice .practice-info .practice-info-box .practice-info-left .total-rewards-pool .total-rewards-box .total-rewards-num .total-rewards-pool-title {
  font-size: 14px;
}
.testnet-practice .practice-info .practice-info-box .practice-info-left .total-rewards-pool .total-rewards-box .total-rewards-num .toatl-rewards-deri {
  font-size: 56px;
  color: #FFFFFF;
  display: flex;
  align-items: flex-start;
}
.testnet-practice .practice-info .practice-info-box .practice-info-left .total-rewards-pool .total-rewards-box .deri-text {
  font-size: 11px;
  color: #93A1B8;
  display: inline-block;
  width: 43px;
  height: 20px;
  border-radius: 100px;
  border: 1px solid rgba(224, 236, 255, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 17px;
  margin-left: 10px;
}
.testnet-practice .practice-info .practice-info-box .practice-info-left .your-pnl {
  height: 109px;
  margin-top: 39px;
  text-align: center;
  border: 1px dashed #93A1B8;
  font-size: 24px;
  color: #93A1B8;
}
.testnet-practice .practice-info .practice-info-box .practice-info-left .your-pnl span div {
  display: inline-block;
}
.testnet-practice .practice-info .practice-info-box .practice-info-left .your-pnl span .text {
  height: 30px;
}
.testnet-practice .practice-info .practice-info-box .practice-info-left .your-pnl .your-pnl-pen {
  margin-left: 16px;
  font-size: 32px;
  color: #FFFFFF;
  font-weight: 600;
  line-height: 109px;
}
.testnet-practice .practice-info .practice-info-box .practice-info-right {
  width: 528px;
}
.testnet-practice .practice-info .practice-info-box .practice-info-right .top-pnl-list-box {
  height: 555px;
}
.testnet-practice .practice-info .practice-info-box .practice-info-right .top-pnl-list-box .top-pnl-list-box-title {
  height: 64px;
  line-height: 64px;
  font-size: 18px;
  color: #E0ECFF;
  font-weight: bold;
  border-bottom: 1px solid #203B60;
}
.testnet-practice .practice-info .practice-info-box .practice-info-right .top-pnl-list-box .top-pnl-list .top-pnl-list-title {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  font-weight: 600;
}
.testnet-practice .practice-info .practice-info-box .practice-info-right .top-pnl-list-box .top-pnl-list .top-pnl-list-info {
  margin-top: 7px;
}
.testnet-practice .practice-info .practice-info-box .practice-info-right .top-pnl-list-box .top-pnl-list .top-pnl-list-info-box {
  display: flex;
  justify-content: space-between;
  height: 32px;
  margin-top: 10px;
  align-items: center;
  font-weight: 600;
  color: #FFFFFF;
}
.testnet-practice .practice-info .practice-info-box .practice-info-right .page-box .remind {
  text-align: right;
}
.testnet-practice .practice-info .practice-info-box .practice-info-right .page-box .pagination {
  border: 0;
}
.testnet-practice .practice-info .practice-info-box .practice-info-right .page-box .pagination .rc-pagination-options {
  display: none;
}
.testnet-practice .detailed-rules {
  width: 1250px;
  margin: 50px auto;
}
@media only screen and (max-width: 1268px) {
  .testnet-practice .practice-info {
    width: 1095px;
  }
  .testnet-practice .practice-info .practice-time,
  .testnet-practice .practice-info .practice-info-box,
  .testnet-practice .practice-info .practice-title {
    width: 1050px;
  }
  .testnet-practice .practice-info .practice-info-box .practice-info-left {
    width: 536px;
  }
  .testnet-practice .practice-info .practice-info-box .practice-info-right {
    width: 472px;
  }
  .testnet-practice .detailed-rules {
    width: 1095px;
  }
}
@media only screen and (max-width: 1113px) {
  .testnet-practice .practice-info {
    width: 707px;
    height: 100%;
  }
  .testnet-practice .practice-info .practice-time,
  .testnet-practice .practice-info .practice-info-box,
  .testnet-practice .practice-info .practice-title {
    width: 538px;
  }
  .testnet-practice .practice-info .practice-info-box {
    flex-wrap: wrap;
  }
  .testnet-practice .practice-info .practice-info-box .practice-info-right {
    width: 100%;
    margin-top: 38px;
  }
  .testnet-practice .detailed-rules {
    width: 707px;
  }
}
@media only screen and (max-width: 724px) {
  .testnet-practice .practice-info {
    width: 357px;
    height: 100%;
  }
  .testnet-practice .practice-info .practice-time,
  .testnet-practice .practice-info .practice-info-box,
  .testnet-practice .practice-info .practice-title {
    width: 342px;
  }
  .testnet-practice .practice-info .practice-info-box {
    flex-wrap: wrap;
  }
  .testnet-practice .practice-info .practice-info-box .practice-info-right {
    width: 100%;
    margin-top: 38px;
  }
  .testnet-practice .practice-info .practice-info-box .practice-info-right .top-pnl-list {
    padding-left: 32px;
    padding-right: 32px;
  }
  .testnet-practice .detailed-rules {
    width: 357px;
  }
}
